@import url(https://fonts.googleapis.com/css?family=Nanum+Gothic+Coding:400,700|Nanum+Myeongjo:400,700,800);
*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
html {
  font-family: sans-serif;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
@-ms-viewport {
  width: device-width;
}
article, aside, figcaption, figure, footer, header, hgroup, main, nav, section {
  display: block;
}
body {
  margin: 0;
  font-family: "Nanum Myeongjo", serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.5;
  color: #282828;
  text-align: left;
  background-color: #fff;
}
[tabindex="-1"]:focus {
  outline: 0 !important;
}
hr {
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  height: 0;
  overflow: visible;
}
h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0;
}
p {
  margin-top: 0;
  margin-bottom: 0;
}
abbr[title],
abbr[data-original-title] {
  text-decoration: underline;
  -webkit-text-decoration: underline dotted;
          text-decoration: underline dotted;
  cursor: help;
  border-bottom: 0;
}
address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit;
}
ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
}
ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}
dt {
  font-weight: 800;
}
dd {
  margin-bottom: .5rem;
  margin-left: 0;
}
blockquote {
  margin: 0 0 1rem;
}
dfn {
  font-style: italic;
}
b,
strong {
  font-weight: bolder;
}
small {
  font-size: 80%;
}
sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}
sub {
  bottom: -.25em;
}
sup {
  top: -.5em;
}
a {
  color: blue;
  text-decoration: underline;
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}
a:hover {
    color: #000080;
    text-decoration: none;
}
a:not([href]):not([tabindex]) {
  color: inherit;
  text-decoration: none;
}
a:not([href]):not([tabindex]):hover, a:not([href]):not([tabindex]):focus {
    color: inherit;
    text-decoration: none;
}
a:not([href]):not([tabindex]):focus {
    outline: 0;
}
pre,
code,
kbd,
samp {
  font-family: "Nanum Gothic Coding", monospace;
  font-size: 1em;
}
pre {
  margin-top: 0;
  margin-bottom: 1rem;
  overflow: auto;
  -ms-overflow-style: scrollbar;
}
figure {
  margin: 0 0 1rem;
}
img {
  vertical-align: middle;
  border-style: none;
}
svg:not(:root) {
  overflow: hidden;
}
table {
  border-collapse: collapse;
}
caption {
  padding-top: 0.3em;
  padding-bottom: 0.3em;
  color: #282828;
  text-align: left;
  caption-side: bottom;
}
th {
  text-align: inherit;
}
label {
  display: inline-block;
  margin-bottom: 0;
}
button {
  border-radius: 0;
}
button:focus {
  outline: 1px dotted;
  outline: 5px auto -webkit-focus-ring-color;
}
input,
button,
select,
optgroup,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}
button,
input {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}
button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}
input[type="radio"],
input[type="checkbox"] {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0;
}
input[type="date"],
input[type="time"],
input[type="datetime-local"],
input[type="month"] {
  -webkit-appearance: listbox;
}
textarea {
  overflow: auto;
  resize: vertical;
}
fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}
legend {
  display: block;
  width: 100%;
  max-width: 100%;
  padding: 0;
  margin-bottom: .5rem;
  font-size: 1.5rem;
  line-height: inherit;
  color: inherit;
  white-space: normal;
}
progress {
  vertical-align: baseline;
}
[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}
[type="search"] {
  outline-offset: -2px;
  -webkit-appearance: none;
}
[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}
output {
  display: inline-block;
}
summary {
  display: list-item;
  cursor: pointer;
}
template {
  display: none;
}
[hidden] {
  display: none !important;
}
.clearfix {
  *zoom: 1;
}
.clearfix:after {
  content: '';
  display: block;
  clear: both;
}
ul {
  margin: 0;
  padding: 0;
}
ul li {
    list-style: none;
    margin: 0.5em 0;
}
#app {
  padding: 1.5em;
  padding-top: 10em;
}
#app > article {
    position: relative;
    z-index: 1;
    max-width: 500px;
}
#app > article > section.content {
      position: relative;
      z-index: 10;
      text-shadow: 0 0 5px white;
}
#app > article h1 {
      font-size: 3em;
      line-height: 1.2;
      word-break: keep-all;
      margin: 15% 0 10% 0;
}
#app > article h2 {
      font-size: 2em;
}
#app > article h2, #app > article h3 {
      margin: 10% 0 5% 0;
}
#app > article p {
      word-break: keep-all;
      margin: 0.5em 0;
}
#app > article img {
      max-width: 100%;
}
#app > article .date {
      text-align: right;
}
#app > article .VueCarousel {
      position: fixed;
      left: 0;
      right: 0;
      top: 50%;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
      width: 25vw;
      margin: 0 auto;
      z-index: 0;
}
#app > article .VueCarousel .VueCarousel-slide img {
        width: 100%;
}
#app > article .VueCarousel .VueCarousel-pagination {
        display: none;
}
#app > article .VueCarousel .VueCarousel-pagination .VueCarousel-dot {
          margin: 0.5em 0 !important;
}
#masthead {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 0;
  padding: 1.5em;
  z-index: 100;
}
#masthead h1, #masthead nav > ul > li, #masthead nav > .show_menu {
    font-size: 1.2em;
}
#masthead h1 {
    float: left;
}
#masthead nav {
    float: right;
    text-align: right;
}
#masthead nav ul {
      margin-top: -0.5em;
}
#masthead nav ul .menu-item {
        display: block;
}
#masthead nav ul.on-sm {
        display: none;
}
@media (max-width: 700px) {
#masthead nav ul.on-lg {
    display: none;
}
#masthead nav ul.on-sm {
    display: block;
}
#app > article {
    width: 100%;
    max-width: inherit;
}
#app > article > section.content {
      position: relative;
}
#app > article .VueCarousel {
      position: relative;
      top: inherit;
      left: inherit;
      right: inherit;
      width: 100%;
      -webkit-transform: none;
              transform: none;
}
#app > article .VueCarousel .VueCarousel-pagination {
        display: block;
}
}


















































































































































































































































































nav {
  display: none;
}
.msg {
  position: fixed;
  top: 50%;
  left: 0;
  right: 0;
  margin: 0 auto;
  max-width: 98vw;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  text-align: center;
}
